}

@-webkit-keyframes color-change {
	0% {
		font-size: 25px;
		color: rgb(158, 38, 38);
	}
	80% {
		font-size: 25px;
		color: rgb(158, 38, 38);
	}
	95% {
		font-size: 25px;
	}
}

@keyframes color-change {
	0% {
		font-size: 25px;
		color: rgb(158, 38, 38);
	}
	80% {
		font-size: 25px;
		color: rgb(158, 38, 38);
	}
	95% {
		font-size: 25px;
	}
}

