	        transform: scaleX(0);
	-webkit-transition: 0.25s linear;
	transition: 0.25s linear;
	border-radius: 5px;
}
.footer p:hover:before,
.footer p:focus:before {
	visibility: visible;
	-webkit-transform: scaleX(1);
	        transform: scaleX(1);
}

